.navbar{
    width: 100%;
    background: linear-gradient(to left, rgb(230, 217, 217), rgb(173, 144, 144), rgb(124, 96, 96));
    justify-content: space-between;
}
.menu-container{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 10px;
}
.nav-links{
    list-style: none;
    display: flex;
    align-items: center;
    margin-left: 20px;    
}
.nav-link{
    text-decoration: none;
    color: white;
    margin-right: 15px;
}
.nav-link:hover, 
.nav-link:focus{
    color: #000000;
    background-color: #d4c0c0;
}
.nav-links li{
    margin: 10px 0;

}
.header {
    position: relative;
    width: 100%;
    height: 500px; 
    overflow: hidden; 
  }
  
  .video-background {
    position: absolute;
    width: 100%;
    height: auto;
    min-width: 100%;
    min-height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
.footer {
    width: 100%;
    background: linear-gradient(to left, rgb(230, 217, 217), rgb(173, 144, 144), rgb(124, 96, 96));
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: white;
    text-align: center;
    padding: 5px;
}