#about-me {
    margin-bottom: 20px;
    padding: 25px;
    background-color: #fcfafa;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }

/* Style för huvudrubriken */
h1 {
    font-size: 2.5em;
    color: #000000;
    text-align: center;
    margin-bottom: 20px;
  }
  
  /* Style för underrubriker */
  h4 {
    font-size: 1.5em;
    color: #000000;
  }
  
  /* Style för vanlig text */
  p {
    font-size: 1.1em;
    line-height: 1.6;
    color: #000000;
  }
  
  /* Style för starkt markerad text */
  strong {
    font-weight: bold;
    color: #000;
  }
  
  /* Style för kursiv text */
  em {
    font-style: italic;
    color: #000000;
  }